import React,{Component} from 'react';
import { withRouter } from 'react-router-dom';
import "./search.css";

const url = "https://marchappapi.herokuapp.com/city";
const hurl = "https://marchappapi.herokuapp.com/hall?city=";

class Search extends Component{
    constructor(){
        super()

        this.state={
            city:'',
            hall:''
        }
    }
    //const url= "https://mymusicapis.herokuapp.com/artist";

    //Display City in option
    renderCity = (data) => {
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item.city}>{item.city} | {item.country}</option>
                )
            })
        }
    }
    renderHall= (data) => {
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item._id}>{item.name} | {item.city}</option>
                )
            })
        }
    }

    handleCity=(event) => {
        console.log(event.target.value)
        const city = event.target.value;
        fetch(`${hurl}${city}`,{method:'GET'})
        .then((res) => res.json())
        .then((data)=> {
            this.setState({hall:data})
        })
    }

    handleHall=(event) => {
        this.props.history.push(`/info/${event.target.value}`)
    }

    render(){
        return(
            <React.Fragment>
                <div className="locationSelector">
                    <select class="dropdown dropdown-dark" onChange={this.handleCity}>
                    <option>. . . . . . . .Search City. . . . . . .</option>
                        {this.renderCity(this.state.city)}
                    </select>
                    <select className="dropdown1" onChange={this.handleHall}>
                        <option>. . . . . . . . . . . . . .Select Theatres. . . . . . . . . . .</option>
                        {this.renderHall(this.state.hall)}            
                    </select>
                </div>
            </React.Fragment>
        )
    }

    //call api to get data
    componentDidMount(){
        fetch(url,{method:'GET'})
        .then((res) => res.json())
        //setting data in state
        .then((data)=> this.setState({city:data}))
    }
}

export default withRouter(Search);