import React, {Component} from 'react';
import axios from 'axios';
import ListingDisplay from './listingDisplay';
import CityFilter from '../filter/cityFilter';
import Navbar from '../Navbar/Navbar';

const url = "https://marchappapi.herokuapp.com/hall?movie=";

class ListingApi extends Component{
    constructor(props){
        super(props)

        this.state={
            halllist:''
        }
    }
    setDataPerFilter=(sortedData)=>{
        this.setState({halllist:sortedData})
    }
    render(){
        console.log(">>>",this.state.halllist)
        return(
           <div className="row" >
                <div className="sim">
                    <Navbar/>
                </div>
                <div style={{marginLeft:'5%'}}>
                    <div className="col-md-2">
                        <CityFilter hallPerCity={(data) => {this.setDataPerFilter(data)}}/>
                    </div>
                    <div className="col-md-10">
                        <ListingDisplay hallsList={this.state.halllist}/>
                    </div>
                </div>
           </div>
        )
    }

    componentDidMount(){
        var movieid = this.props.match.params.id
        sessionStorage.setItem('movieId',movieid)
        axios.get(`${url}${movieid}`)
        .then((response) => {this.setState({halllist:response.data})})
    }
}


export default ListingApi;