import React, {Component} from 'react';
import axios from 'axios';
import Navbar from '../Navbar/Navbar';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import {Link} from 'react-router-dom';

const url ="https://marchappapi.herokuapp.com/hall"

class Details extends Component{
    constructor(){
        super()
        
        this.state={
            details:''
        };
    }

    render(){
        let {details} = this.state
        return(
            <>
                <Navbar/>
                <div className="container">
                    <div className="panel panel-primary">
                        <div className="panel-heading">
                            <h2>{this.state.details.name}</h2>
                        </div>
                        <div className="panel-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <img className="img-responsive" src={details.thumb}
                                        style={{height:400,width:1800}}/>
                                </div>
                                <div className="col-md-6">
                                    <h3><b>{details.name}</b></h3>
                                    <h4>{details.city}</h4>
                                </div>
                            </div>
                            <hr/>
                            <Tabs>
                                <TabList>
                                    <Tab style={{fontSize:'15px'}}>About</Tab>
                                    <Tab style={{fontSize:'15px'}}>Address</Tab>
                                </TabList>
                                <TabPanel>
                                    <h2>About This Theatre:</h2>
                                    <p style={{fontSize:'12px'}}>{details.name}  is a multiplex chain in India in 120 cities at 162 locations with over 470 operational screens. {details.name} caters to over 5 crore patrons annually and has a total seating capacity of over 1,50,000. {details.name} has recently added two more properties into the overseas market with opening of four new screens in Singapore with a total of 6 screens there. Their vision is to hold 1000 screens globally by 2020 .</p>
                                </TabPanel>
                                <TabPanel>
                                    <h5>{details.name},</h5><h5>4.5 / 5.0</h5>
                                    <h5>{details.address},</h5>
                                    <h5>{details.city},</h5><h5>755004.</h5>
                                    <h5>phone- 9456482452</h5>
                                </TabPanel>
                            </Tabs>
                            <Link to="/" className="btn btn-success" style={{fontSize:'15px'}}>Back</Link> &nbsp;
                        <Link to={`/booking/${details.name}`} className="btn btn-danger" style={{fontSize:'15px'}}>Book Tickets</Link>
                        </div>
                    </div>
                </div>
            </>
        )
    }

    componentDidMount(){
        const hallsId=this.props.match.params.id
        axios.get(`${url}/${hallsId}`)
        .then((res) => {this.setState({details:res.data[0]})})
    }
}

export default Details;