import React from 'react';
import { Link } from 'react-router-dom';
import './listing.css';

const ListingDisplay = (props) => {
    const renderList = ({hallsList}) => {
        if(hallsList){
            return hallsList.map((item) => {
                return(
                    <div class="tileContainer1">
                        <Link to={`/details/${item._id}`}>
                            <div class="tileComponent2">
                                <img src={item.thumb}/>
                            </div>
                        </Link>
                        <div class="componentHeading1">
                            <h3 style={{fontSize:'25px'}}>{item.name}</h3>
                            <hr style={{backgroundColor:'black',borderColor:'black'}}/>
                            <br/>
                            <b>
                                {item.city}
                            <br/>
                                {item.address}
                            </b>
                        </div>
                        
                </div>
                )
            })
        }else{
            return(
                <div className="load">
                    <img src="/images/loaderr.gif"/>
                </div>
            )
        }

    }
    return(
       <>
            {renderList(props)}
       </>
    )
}

export default ListingDisplay;