import React, { Component } from 'react'  
import Carousel from 'react-bootstrap/Carousel'

export class BootstrapCarousel extends Component {
    render() {  
          return (  
                <>  
                    <div className='container-fluid'>  
                        <Carousel>  
                            <Carousel.Item style={{'height':"300px"}} >  
                                <img style={{'height':"300px"}}  
                                className="d-block w-100"  
                                src={'https://i.ibb.co/Kjc0Ytm/sunburn-home-festival-20-12-02-2021-08-56-56-785.jpg'}  />  
                            </Carousel.Item>  
                        <Carousel.Item style={{'height':"300px"}}>  
                            <img style={{'height':"300px"}}  
                            className="d-block w-100"  
                            src={'https://i.ibb.co/m0mhXS6/image14.jpg'}    />    
                        </Carousel.Item>  
                        <Carousel.Item style={{'height':"300px"}}>  
                            <img style={{'height':"300px"}}  
                            className="d-block w-100"  
                            src={'https://i.ibb.co/RgnPB5z/4.jpg'}   />   
                        </Carousel.Item>
                        <Carousel.Item style={{'height':"300px"}}>  
                            <img style={{'height':"300px"}}  
                            className="d-block w-100"  
                            src={'https://i.ibb.co/GPTthCP/5.jpg'}   />   
                        </Carousel.Item>
                        <Carousel.Item style={{'height':"300px"}}>  
                            <img style={{'height':"300px"}}  
                            className="d-block w-100"  
                            src={'https://i.ibb.co/BchtVkg/1.jpg'}   />   
                        </Carousel.Item>
                        <Carousel.Item style={{'height':"300px"}}>  
                            <img style={{'height':"300px"}}  
                            className="d-block w-100"  
                            src={'https://i.ibb.co/fC2pBV8/2.jpg'}   />   
                        </Carousel.Item>
                    </Carousel>  
                </div>
            </>  
        )
    }
}  

export default BootstrapCarousel