import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import Home from './component/Home/Home';
import Listing from './component/listing/listingApi';
import Details from './component/details/HallDetails';
import Covid from './component/Covid/CovidRule';
import Contact from './component/Navbar/Contact';
import About from './component/About/About';
import Detail from './component/info/Hallinfo';
import PlaceBooking from './component/booking/placeBooking';
import DisplayApi from './component/booking/DisplayApi';
import Video from './component/trailers/trailer';

const Routing = () => {
    return(
        <BrowserRouter> 
            <Route exact path="/" component={Home}/>
            <Route path="/listing/:id" component={Listing}/>
            <Route path="/Covid/:id" component={Covid}/>
            <Route path="/details/:id" component={Details}/>
            <Route path="/Navbar/:id" component={Contact}/>
            <Route path="/About" component={About}/>
            <Route path="/info/:id" component={Detail}/>
            <Route path="/trailers" component={Video}/>
            <Route path="/viewbooking" component={DisplayApi}/>
            <Route path="/booking/:name" component={PlaceBooking}/>
        </BrowserRouter>
    )
}

export default Routing;