import React,{Component} from 'react';
import axios from 'axios';
import { GiModernCity } from "react-icons/gi";
import './filter.css';

const url = "https://marchappapi.herokuapp.com/hall?movie=";

class CityFilter extends Component {
    Cityfilter = (event) => {
        let movieId=sessionStorage.getItem('movieId')
        let cityId = event.target.value;
        let cityUrl;
        if(cityId==''){
            cityUrl=`${url}${movieId}`
        }
        else{
            cityUrl=`${url}${movieId}&city=${cityId}`
        }
        axios.get(cityUrl)
            .then((response) => {this.props.hallPerCity(response.data)})
    }
    render() {
        return(
            <React.Fragment>
                <div style={{marginTop:'25px'}}>
                    <center><GiModernCity color='navy'fontSize='35px'/></center>
                </div>
                    <div className="filter">
                    <div onChange={this.Cityfilter}>
                        <label className="radio">
                            <input type="radio" value="" name="city"/>All
                        </label>
                        <label className="radio">
                            <input type="radio" value="Delhi" name="city"/>Delhi
                        </label>
                        <label className="radio">
                            <input type="radio" value="Mumbai" name="city"/>Mumbai
                        </label>
                        <label className="radio">
                            <input type="radio" value="Pune" name="city"/>Pune
                        </label>
                        <label className="radio">
                            <input type="radio" value="Bangalore" name="city"/>Bangalore
                        </label>
                        <label className="radio">
                            <input type="radio" value="Kolkata" name="city"/>Kolkata
                        </label>
                        <label className="radio">
                            <input type="radio" value="Bhubaneswar" name="city"/>Bhubaneswar
                        </label>
                        <label className="radio">
                            <input type="radio" value="Chennai" name="city"/>Chennai
                        </label>
                        <label className="radio">
                            <input type="radio" value="Hyderabad" name="city"/>Hyderabad
                        </label>
                        <label className="radio">
                            <input type="radio" value="Vishakhapatnam" name="city"/>Vishakhapatnam
                        </label>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default CityFilter;