import React, {Component} from 'react';
import {MenuItems} from "./Menuitem";
import './Navbar.css';
import { Button } from "./Button";
import { Link } from 'react-router-dom';

class Navbar  extends Component {

    state = {clicked: false}

    handleClick = () => {
        this.setState({clicked:!this.state.clicked})
    }

/*conditionalButton =() => {
    if(sessionStorage.getItem('username') == null || sessionStorage.getItem('username') == undefined){
        return (
            <a href='https://github.com/login/oauth/authorize?client_id=b7eaea96fc7aab577c6b'>Sign In</a>
        )
    }else{
        <>
            <img src={this.state.imgurl} style={{height:80,width:75}}/>
            Hi {sessionStorage.getItem('username')}
        </>
    }
}*/

    render() {
        return(
            <nav className="NavbarItems">
                <Link to="/"><h1 className="navbar-logo font-effect-emboss">Cine Castle<i className="fas fa-film"></i></h1></Link>
                <div className="menu-icon" onClick={this.handleClick}>
                    <i className={this.state.clicked ?'fas fa-times':'fas fa-bars'}></i>
                </div>
                <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
                    {MenuItems.map((item, index) =>{
                        return(
                            <li key={index}>
                                <a className={item.cName} href={item.url}>
                                    {item.title}
                                </a>
                            </li>
                        )
                    })}
                </ul>
                
            </nav>
        )
    }
}

export default Navbar;