import React, {Component} from 'react';
import axios from 'axios';
import Navbar from '../Navbar/Navbar';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import {Link} from 'react-router-dom';
import "./halli.css";

const url ="https://marchappapi.herokuapp.com/hall"

class Detail extends Component{
    constructor(){
        super()
        
        this.state={
            details:'',
            Movie:['','','','']
        };
    }

    render(){
        let {details} = this.state
        return(
            <>
                <Navbar/>
                <div className="container">
                    <div className="panel panel-primary">
                        <div className="panel-heading">
                            <h2>{this.state.details.name}</h2>
                        </div>
                        <div className="panel-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <img className="img-responsive" src={details.thumb}
                                        style={{height:400,width:1800}}/>
                                </div>
                                <div className="col-md-6">
                                    <h3><b>{details.name}</b></h3>
                                    <h4>{details.city}</h4>
                                </div>
                            </div>
                            <hr/>
                            <Tabs>
                                <TabList>
                                    <Tab style={{fontSize:'15px'}}>Movies</Tab>
                                    <Tab style={{fontSize:'15px'}}>Address</Tab>
                                </TabList>
                                <TabPanel>
                                    <Link to={`/booking/${details.name}`}>
                                        <div class="tileContainerA">
                                            <div class="tileComponent11">
                                                <h4>{this.state.Movie[0].name}</h4>
                                                <img className="img-responsive" src={this.state.Movie[0].thumb}  style={{height:177,width:180}}/>
                                            </div>
                                        </div>
                                    </Link>
                                    <Link to={`/booking/${details.name}`}>
                                        <div class="tileContainerA">
                                            <div class="tileComponent11">
                                                <h4>{this.state.Movie[1].name}</h4>
                                                <img className="img-responsive" src={this.state.Movie[1].thumb}  style={{height:177,width:180}}/>
                                            </div>
                                        </div>
                                    </Link>
                                    <Link to={`/booking/${details.name}`}>
                                        <div class="tileContainerA">
                                            <div class="tileComponent11">
                                                <h4>{this.state.Movie[2].name}</h4>
                                                <img className="img-responsive" src={this.state.Movie[2].thumb}  style={{height:177,width:180}}/>
                                            </div>
                                        </div>
                                    </Link>
                                </TabPanel>
                                <TabPanel>
                                    
                                    <h5>{details.address},</h5>
                                    <h5>{details.city},</h5><h5>755004.</h5>
                                    <h5>phone- 9456482452</h5>
                                </TabPanel>
                            </Tabs>
                        </div>
                    </div>
                </div>
            </>
        )
    }

    componentDidMount(){
        const hallsId=this.props.match.params.id
        axios.get(`${url}/${hallsId}`)
        .then((res) => {
            let Movie = res.data[0].Movie
            this.setState({details:res.data[0],Movie:Movie})})
    }
}

export default Detail;