import React, { Component } from 'react';
import Navbar from './Navbar';
import { MdEmail } from 'react-icons/md';
import { FcCellPhone } from 'react-icons/fc';
import { GrMapLocation } from 'react-icons/gr';
import { FaFacebookF , FaInstagram ,  FaYoutube , FaPinterestP , FaLinkedinIn , FaTwitter } from "react-icons/fa";


class Contact extends Component {
    render() {
        return (
            <>
            <div>
            <div className="detail" style={{textAlign:'center'}}>
                <Navbar/>
                <h2>ADDRESS:-</h2>
                <hr/>
                <GrMapLocation fontSize='25px'/>
                <h4>3030 N. Rocky Point Dr. W. #600
                Tampa, FL 33607.</h4>
                <hr/>
                <h2>CONTACT INFORMATION:-</h2>
                <hr/>
                <MdEmail fontSize='25px'/>
                <h4>info@cinema.com</h4>
                <h4>info@cinecastle.com</h4>
                <FcCellPhone fontSize='28px'/>
                <h4>+89, 9997775550</h4>
                <h4>8990_222_8990</h4>
                <hr/>
                <h2>SOCIAL:-</h2>
                <hr/>
                <FaFacebookF fontSize='25px'/>
                &nbsp;
                <FaInstagram fontSize='25px'/>
                &nbsp;
                <FaTwitter fontSize='25px'/>
                &nbsp;
                <FaPinterestP fontSize='25px'/>
                &nbsp;
                <FaLinkedinIn fontSize='25px'/>
                &nbsp;
                <FaYoutube fontSize='25px'/>
            </div>
            </div>
            
            </>
        )
    }
}

export default Contact;