import React, { Component } from 'react';
import Navbar from '../Navbar/Navbar';
import { FaUsers } from "react-icons/fa";
import { FcGlobe } from "react-icons/fc";
import { AiOutlineFundProjectionScreen } from "react-icons/ai";
import './about.css'

class About extends Component {
    render() {
        return (
            <>
            <Navbar/>
            <div>
                <img className="img" src={`/images/About.png`}/>
            </div>
            <p style={{textAlign:"center",fontSize:'16PX'}}><strong>Are you a movie buff?</strong> If it's a no, then sorry to call you out on your bluff. Come on, there are endless reasons for watching a movie: for unwinding after a long day, escaping reality for a couple of hours, having a good laugh, spending time with friends, and the list goes on and on. One platform that unites all movie-goers is <strong>CINE CASTLE</strong>, a platform that offers a plethora of options when it comes to booking tickets. 22 years ago in South Africa a seed of an idea was planted, a dream was shared. Inception happened. 22 years on, we look back at what we've built. Leave it up to us, and we'd love to do it all over again.</p><br/>
            <p style={{textAlign:"center",fontSize:'16PX'}}>It founded in <strong>2021.</strong></p>
            <p style={{textAlign:"center",fontSize:'16PX'}}>It's Founder is <strong>RAJESH KUMAR NAYAK.</strong></p>
            <p style={{textAlign:"center",fontSize:'16PX'}}>It's Headquarter is <strong>Washington.</strong></p>
            <br/>
            <h3 style={{color:'black',textAlign:'center'}}>
                <u>Quick Facts :</u><br/>
            </h3><br/>
            <div className="fact">
                <span style={{float:'left',color:'brown',fontSize:'20px',marginLeft:'15px'}}><FaUsers fontSize="38px"/><FaUsers fontSize="38px"/><br/>30 Million+
                CUSTOMERS</span>
                <span style={{float:'left',fontSize:'20px',marginLeft:'29%'}}><FcGlobe fontSize="38px"/><br/>5
                COUNTRIES</span>
                <span style={{float:'right',fontSize:'20px',color:'teal',marginRight:'15px'}}><AiOutlineFundProjectionScreen fontSize="38px"/><br/>5000+
                SCREENs</span>
            </div>
            </>
        )
    }
}

export default About;