import React from 'react';
import ReactPlayer from "react-player";
import './QuickSearch.css';
import {Link} from 'react-router-dom';
import { RiCustomerService2Fill } from 'react-icons/ri';
import { FaFacebookF , FaInstagram ,  FaYoutube , FaPinterestP , FaLinkedinIn , FaTwitter } from "react-icons/fa";


const QuickSearch =(props) => {    
    const renderList = ({quickData}) => {
        if(quickData){
            return quickData.map((item) => {
                return(
                    <Link to={`/listing/${item._id}`}>
                    <div class="tileContainer">
                        <div class="tileComponent1">
                            <img src={`/images/${item.name}.jpg`} className="imageStyle"/>
                        </div>
                        <div class="componentHeading">
                            {item.name}
                        </div>
                        <div class="componentSubHeading">
                            {item.genre}
                        </div>
                    </div>
                </Link>
                )
            })
        }
    }

    return(
        <>
            <div class="quickSearchContainer"> 
                <p class="quickSearchHeading">
                    Recommended Movies :
                </p>
                {renderList(props)}
            </div>
            <ReactPlayer url="https://youtu.be/8y1gDf5_TfM" width='100%'/>
            <Link to={`/Covid/CovidRule`}>
                <div className="imge">
                    <img src={`/images/imagee.jpg`} style={{width:'100%',height:'200px'}}/>
                </div>
            </Link>
            <br/>
            <ReactPlayer url="https://youtu.be/Fri-NCuNL5w" width='100%'/>
            <div className="note">
                <center><h2>Privacy Notes</h2></center>
                <p style={{fontSize:13}}> By using www.bookmyshow.org(our website), you are fully accepting the Privacy Policy available at https://bookmyshow.org/privacy governing you access to bookmyshow and provision of services by Bookmyshow to you If you donot accept terms mentioned in the Privacy policies, you must not share any of your personal information and immidiately exit Bookmyshow.</p>
            </div>
            <br/>
            <div className="help">
                <a href="/Navbar/:id">
                &emsp;  &emsp; &emsp;<RiCustomerService2Fill color="white" fontSize="28px"/>
                    _24/7
                </a>
                <a href="/">
                    &emsp; &emsp; &emsp; &emsp; <span class="glyphicon glyphicon-envelope"  style={{color:'white'}}></span>
                    _Subscribe to the Newsletter
                </a>
            </div>
            <br/>
            <div className="icon">
                <a href="https://www.facebook.com/" style={{color:'blue'}} target="_blank"><FaFacebookF/></a>&ensp;
                <a href="https://www.instagram.com/" style={{color:'rgb(205,72,107)'}} target="_blank"><FaInstagram/></a>&ensp;
                <a href="https://www.twitter.com/" style={{color:'rgb(29,161,242)'}} target="_blank"><FaTwitter/></a>&ensp;
                <a href="https://www.pinterest.com/" style={{color:'brown'}} target="_blank"><FaPinterestP/></a>&ensp;
                <a href="https://www.linkedin.com/" style={{color:'navy'}} target="_blank"><FaLinkedinIn/></a>&ensp;
                <a href="https://www.youtube.com/" style={{color:'red'}} target="_blank"><FaYoutube/></a>
            </div>
            <div className="info">
            <center>
                <h5>Copyright 2021 &copy; Bigtrees Entertanmnt Pvt. Ltd. All Rights Reserved .</h5>
                <h5>The Content and Images used on this site are copyrights vests with the respective owners. The usage of the content and images on this website is intended to promote the work and no endorsement of the artist shall be implied. Unauthorized use is prohibited and punishable by the law. </h5>
            </center>
        </div>
        </>
        )
    }

export default QuickSearch;