import QuickApi from '../searchBar/QuickApi';

const Home = () => {
    return(
        <div className="body">
            <Navbar/>
            <Search/>
            <BootstrapCarousel></BootstrapCarousel>
            <QuickApi/>
        </div>
    )
}

export default Home;