import React,{Component} from 'react';
import Navbar from '../Navbar/Navbar';
import './booking.css';

const BookingUrl = "https://marchappapi.herokuapp.com/placeorder"

class PlaceBooking extends Component{
    constructor(props){
        super(props)

        this.state={
            hall_name:this.props.match.params.name,
            name:sessionStorage.getItem('username'),
            phone:'',
        }
    }

    handleSubmit = () => {
        console.log(this.state)
        fetch(BookingUrl,
            {
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
            .then(this.props.history.push('/viewBooking'))
    }

    handleChange=(event) => {
        this.setState({[event.target.name]:event.target.value})
    }

    render(){
        return(
            <>
            <Navbar/>
            <br/>
            <div className="container">
                <div className="panel panel-info">
                    <div className="panel-heading">
                        Book Tickets
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>Theatre Name</label>
                            <input name="hall_name" value={this.state.hall_name} readOnly
                            className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Date of Booking</label>
                            <input type="Date" name="seat_booking" value="2021-03-11" min="2021-03-13" max="2021-03-20"
                            className="form-control"
                            onChange={this.handleChange} required/>
                        </div>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" name="name" value={this.state.name}
                            className="form-control"
                            onChange={this.handleChange} required/>
                        </div>
                        <div className="form-group">
                            <label>Phone</label>
                            <input type="number" name="phone" value={this.state.number}
                            className="form-control"
                            onChange={this.handleChange} required/>
                        </div>
                        <div className="form-group">
                            <label>Number of seats</label>
                            <input type="Number" name="seats" value={this.state.number}
                            className="form-control"
                            onChange={this.handleChange} required/>
                        </div>
                        
                        <br/><br/>
                        <div className="form-group">
                        <label>Select seats</label>
                            <table id="seatsBlock">
                            <tr>
                                <td colspan="14">
                                    <div class="screen">
                                        SCREEN this way
                                    </div>
                                </td>
                                <br/>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>1</td>
                                    <td>2</td>
                                    <td>3</td>
                                    <td>4</td>
                                    <td>5</td>
                                    <td></td>
                                    <td>6</td>
                                    <td>7</td>
                                    <td>8</td>
                                    <td>9</td>
                                    <td>10</td>
                                </tr>
                                <tr>
                                    <td>A</td>
                                    
                                    <td><input type="checkbox" class="seat" name="seat" value="A1"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A2"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A3"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A4"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A5"/></td>
                                    <td class="seatGap"></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A6"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A7"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A8"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A9"/></td>
                                    <td><input type="checkbox" class="seat" name="seat" value="A10"/></td>
                                </tr>
                                <td>Delux-Rs.100/-</td>
                                <tr>
                                    <td>B</td>
                                    <td><input type="checkbox" class="seat" value="B1"/></td>
                                    <td><input type="checkbox" class="seat" value="B2"/></td>
                                    <td><input type="checkbox" class="seat" value="B3"/></td>
                                    <td><input type="checkbox" class="seat" value="B4"/></td>
                                    <td><input type="checkbox" class="seat" value="B5"/></td>
                                    <td></td>
                                    <td><input type="checkbox" class="seat" value="B6"/></td>
                                    <td><input type="checkbox" class="seat" value="B7"/></td>
                                    <td><input type="checkbox" class="seat" value="B8"/></td>
                                    <td><input type="checkbox" class="seat" value="B9"/></td>
                                    <td><input type="checkbox" class="seat" value="B10"/></td>
                                </tr>
                                <tr>
                                    <td>C</td>
                                    <td><input type="checkbox" class="seat" value="C1"/></td>
                                    <td><input type="checkbox" class="seat" value="C2"/></td>
                                    <td><input type="checkbox" class="seat" value="C3"/></td>
                                    <td><input type="checkbox" class="seat" value="C4"/></td>
                                    <td><input type="checkbox" class="seat" value="C5"/></td>
                                    <td></td>
                                    <td><input type="checkbox" class="seat" value="C6"/></td>
                                    <td><input type="checkbox" class="seat" value="C7"/></td>
                                    <td><input type="checkbox" class="seat" value="C8"/></td>
                                    <td><input type="checkbox" class="seat" value="C9"/></td>
                                    <td><input type="checkbox" class="seat" value="C10"/></td>
                                </tr>
                                <tr>
                                    <td>D</td>
                                    <td><input type="checkbox" class="seat" value="D1"/></td>
                                    <td><input type="checkbox" class="seat" value="D2"/></td>
                                    <td><input type="checkbox" class="seat" value="D3"/></td>
                                    <td><input type="checkbox" class="seat" value="D4"/></td>
                                    <td><input type="checkbox" class="seat" value="D5"/></td>
                                    <td></td>
                                    <td><input type="checkbox" class="seat" value="D6"/></td>
                                    <td><input type="checkbox" class="seat" value="D7"/></td>
                                    <td><input type="checkbox" class="seat" value="D8"/></td>
                                    <td><input type="checkbox" class="seat" value="D9"/></td>
                                    <td><input type="checkbox" class="seat" value="D10"/></td>
                                </tr>
                                <td>Super Delux-Rs.140/-</td>
                                <tr>
                                    <td>E</td>
                                    <td><input type="checkbox" class="seat" value="E1"/></td>
                                    <td><input type="checkbox" class="seat" value="E2"/></td>
                                    <td><input type="checkbox" class="seat" value="E3"/></td>
                                    <td><input type="checkbox" class="seat" value="E4"/></td>
                                    <td><input type="checkbox" class="seat" value="E5"/></td>
                                    <td></td>
                                    <td><input type="checkbox" class="seat" value="E6"/></td>
                                    <td><input type="checkbox" class="seat" value="E7"/></td>
                                    <td><input type="checkbox" class="seat" value="E8"/></td>
                                    <td><input type="checkbox" class="seat" value="E9"/></td>
                                    <td><input type="checkbox" class="seat" value="E10"/></td>
                                </tr>
                                <tr>
                                    <td>F</td>
                                    <td><input type="checkbox" class="seat" value="F1"/></td>
                                    <td><input type="checkbox" class="seat" value="F2"/></td>
                                    <td><input type="checkbox" class="seat" value="F3"/></td>
                                    <td><input type="checkbox" class="seat" value="F4"/></td>
                                    <td><input type="checkbox" class="seat" value="F5"/></td>
                                    <td></td>
                                    <td><input type="checkbox" class="seat" value="F6"/></td>
                                    <td><input type="checkbox" class="seat" value="F7"/></td>
                                    <td><input type="checkbox" class="seat" value="F8"/></td>
                                    <td><input type="checkbox" class="seat" value="F9"/></td>
                                    <td><input type="checkbox" class="seat" value="F10"/></td>
                                </tr>
                                <td>Recliner-Rs.180/-</td>
                                <tr class="seatVGap"></tr>
                                <tr>
                                    <td>G</td>
                                    <td><input type="checkbox" class="seat" value="G1"/></td>
                                    <td><input type="checkbox" class="seat" value="G2"/></td>
                                    <td><input type="checkbox" class="seat" value="G3"/></td>
                                    <td><input type="checkbox" class="seat" value="G4"/></td>
                                    <td><input type="checkbox" class="seat" value="G5"/></td>
                                    <td></td>
                                    <td><input type="checkbox" class="seat" value="G6"/></td>
                                    <td><input type="checkbox" class="seat" value="G7"/></td>
                                    <td><input type="checkbox" class="seat" value="G8"/></td>
                                    <td><input type="checkbox" class="seat" value="G9"/></td>
                                    <td><input type="checkbox" class="seat" value="G10"/></td>
                                </tr>
                                <td>VIP-Rs.300/-</td>
                            </table>
                        </div>
                    </div> 
                        <button className="btn btn-success"
                            onClick={this.handleSubmit}>
                                Confirm Booking
                        </button>
                </div>
                </div>
            </>
        )
    }
}

export default PlaceBooking;