import React,{Component} from 'react';
import QuickSearch from './QuickSearch';

const url = "https://marchappapi.herokuapp.com/movie";


class QuickApi extends Component{

    constructor(){
        super()

        this.state={
            movieType:''
        }
    }

    render(){
        return(
            <QuickSearch quickData={this.state.movieType}/>
        )
    }

    componentDidMount(){
        fetch(url,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({movieType:data})
        })
    }
}

export default QuickApi;